import React from 'react'
import TestView from './TestView/TestView'
import images from '../imagesCompressedRecepcion'
import originals from '../imagesOriginalRecepcion'
/**
* @author
* @function Recepcion
**/

const Recepcion = () => {
    let n = 1
    let nbefore = 1
  //the arrangements of the image blocks
  const arrngements = {
    1:[4,8],
    2:[8,4],
    3:[6,6]
//    4:[4,4,4]
  }
    const getRandomInt = (min, max) => {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min;
      }
  return(
    <div className="row align-items-stretch">
        { images.map( (image, i) => { 
                  //if i is pair get a new random arrangement
                  if (i % 2 == 0) {
                    n = getRandomInt(1,3)
                    while (n === nbefore) {
                      n = getRandomInt(1,3)
                    }
                  }
                  nbefore = n
                  return (<TestView
                  image={image}
                  original = {originals[i]}
                  key={i}
                  i = {i}
                  group='recepcion'
                  random = {arrngements[n][i%2]} //i%2 will produce the position of 0 and then 1
                  />)
                  })}
    </div>
   )

 }


export default Recepcion