import './App.css';
import { useEffect, useState } from 'react';

import SiteMobileMenu from './views/SiteMobileMenu'
import Menu from './views/menu/Menu'
import {
  LightgalleryProvider
} from "react-lightgallery";
import { Route, Switch, Redirect } from 'react-router-dom';
import { BrowserRouter as Router } from 'react-router-dom';
import Huasteca from './views/Huasteca'
import Recepcion from './views/Recepcion'


function App() {

  const [changed, setChagned] = useState(true)
  const [loadingComponent, setLoadingComponent] = useState(true)


  useState(() =>{
    setTimeout(()=>{
      console.log("timeout reached")
      setLoadingComponent(false)
    },1000)
  },[])

  const changeImages = (bool) => { // the callback. Use a better name
    setChagned(bool)
    console.log(`Change images ${bool}`)

  };

  return (
    <div>

      <LightgalleryProvider
            onBeforeOpen={() => console.info("onBeforeOpen")}
            onAfterOpen={() => console.info("onAfterOpen")}
            onSlideItemLoad={() => console.info("onSlideItemLoad")}
            onBeforeSlide={() => console.info("onBeforeSlide")}
            onAfterSlide={() => console.info("onAfterSlide")}
            onBeforePrevSlide={() => console.info("onBeforePrevSlide")}
            onBeforeNextSlide={() => console.info("onBeforeNextSlide")}
            onDragstart={() => console.info("onDragstart")}
            onDragmove={() => console.info("onDragmove")}
            onDragend={() => console.info("onDragend")}
            onSlideClick={() => console.info("onSlideClick")}
            onBeforeClose={() => console.info("onBeforeClose")}
            onCloseAfter={() => console.info("onCloseAfter")}
          >
        <Router>
      <div className="site-wrap">
        <SiteMobileMenu/>
        <Menu
          changeImages = {changeImages}
        />
      <main className="main-content">
          <div className="container-fluid photos">
            <div className="row align-items-stretch">
              <Switch>
                  <Route exact path="/react-album/#/Huasteca" component={Huasteca} />
                  <Route exact path="/">
                      <Redirect to="/react-album/#/Huasteca" />
                  </Route>
                  <Route exact path="/react-album/#/Recepcion" component={Recepcion}/>
              </Switch>
            </div>
          </div>
      </main>
      </div>
      </Router>
      </LightgalleryProvider>
    </div>
  );
}

export default App;


